import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {BasicAuthService} from "../service/basic-auth.service";


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username = 'randomThing';
  password = '';
  message = '';
  errorMessage = 'invalid login';
  invalidUser = false;
  currUser = ''


  constructor(private router: Router,
              private basicAuthService: BasicAuthService
  ) {
  }

  ngOnInit(): void {
  }



  CheckLogin() {

    this.basicAuthService.executeAuthService(this.username, this.password).subscribe(
      data => {
        console.log(data)
        this.router.navigate(['welcome', this.username])
        this.invalidUser = false

      },
      error => {
        console.log(error)
        this.invalidUser = true
      }
    )

    if (this.basicAuthService.executeAuthService(this.username, this.password)) {
      this.router.navigate(['welcome', this.username]);
      sessionStorage.setItem('currUser', this.username)
    } else {
      this.invalidUser = true;
    }
  }


}
